import * as React from 'react'
import bars from '../../assets/bars.png'
//import './Card.css'
import {Button,Card,Icon,Grid} from "@mui/material"
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';

const Tarjeta = (props) => {
    return (
    <Grid item mt={2} mb={2} md={4} sm={12} lg={3}>
        <Card>
            <div className="card-header">
                <img src={props.img} alt="" />
            </div>
            <div className="card-body">
                <h5>{props.marca}</h5>
                <h4 className="card-title">{props.titulo}</h4>
                <p>Cdigo: {props.codigo}</p>
                <p>Stock: {props.stock}</p>
                <div>
                    <span className="tag">PRECIO ONLINE</span>
                    <span className="price">S/ {props.ponline}</span>
                </div>
                <p>Normal S/ {props.precio}</p>
            </div>
            <div className="card-footer">
                <Button fullWidth variant="contained" color="primary" startIcon={<ShoppingCartIcon/>}>
                    Comprar
                </Button>
            </div>
        </Card>
    </Grid>
    )
}
export default Tarjeta