import React from 'react';
import './Header.css';
import logo from '../../assets/logo.webp';
import {Link} from "react-router-dom"
const Header = () =>{
    return (
        <header>
            <div class="oferta">    
                <b>Empez Cyber Wow!</b> Del 4 al 8 de julio encuentra las mejores ofertas<b> AQU</b>  
            </div>
            <div class="nav">
                <img src={logo}/>
                <ul>
                    <li><a href="#">Ingresar</a></li>
                    <li><a href="#">Mi carro</a></li>
                    <li><Link className="navbar-brand" to="/">Principal</Link></li>
                    <li><Link className="navbar-brand" to="detalle">Detalle</Link></li>
                </ul>
            </div>
        </header>
    )
}
export default(Header);